import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-select.scss';
import '@servicenow/now-dropdown';
import '@servicenow/now-icon';
import '@servicenow/now-popover';
import {
	getFormFieldMessageIds,
	renderFormFieldMessages,
	renderFormFieldLabel
} from '@servicenow/now-input';
import {messagesSchema} from '@servicenow/now-input';
import focusBehavior from '@servicenow/behavior-focus';
import {itemsSchema, constrainSchema} from '@servicenow/now-dropdown';
import {makeRedispatchEffect} from '@servicenow/library-enhanced-utils';
import {flatMap, pick, transform, isNumber} from 'lodash';
import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_DOM_TREE_READY} = actionTypes;

const getSelected = (items, valueId) => {
	const selected = flattenItems(items).find((item) => item.id === valueId);
	if (selected) {
		return selected.label;
	}
};

const flattenItems = (items) => flatMap(items, (item) => item.children || item);

const focusTrigger = (host) => {
	const trigger = host.shadowRoot.querySelector('.now-select-trigger');
	if (trigger) {
		trigger.focus();
	}
};

const getDropdownListConstrain = (constrain) => {
	return transform(constrain, (result, value, key) => {
		if (key === 'height' || key === 'minHeight' || key === 'maxHeight') {
			result[key] = isNumber(value) ? `${value}px` : value;
		}
	});
};

const view = (state, {dispatch}) => {
	const {componentId, opened, focusedItem} = state;
	const {
		search,
		disabled,
		hideRequiredIndicator,
		invalid,
		items,
		itemsListConstrain,
		required,
		optional,
		label,
		helperContent,
		messages,
		selectedItem
	} = state.properties;

	const selectedLabel = getSelected(items, selectedItem);
	const constrain = {
		maxHeight: 300,
		width: 'target',
		...itemsListConstrain
	};

	return (
		<div className="now-select">
			{renderFormFieldLabel(
				componentId,
				invalid,
				required,
				optional,
				label,
				helperContent,
				hideRequiredIndicator
			)}
			<now-popover
				hide-tail
				constrain={pick(constrain, ['width', 'minWidth', 'maxWidth'])}
				manage-opened
				opened={opened}
				interaction-type="listbox"
				append-to-payload={{isListbox: true}}
				disable-scroll>
				{/* eslint-disable-next-line jsx-a11y/role-supports-aria-props, jsx-a11y/aria-activedescendant-has-tabindex */}
				<button
					slot="trigger"
					id={`form-field-${componentId}`}
					class={{
						'now-select-trigger': true,
						'now-form-field': true,
						'is-invalid': invalid,
						'is-disabled': disabled,
						'is-open': opened
					}}
					aria-haspopup="listbox"
					aria-expanded={String(opened)}
					aria-labelledby={`form-field-${componentId}-label form-field-${componentId}`}
					aria-describedby={getFormFieldMessageIds(componentId, messages)}
					aria-disabled={disabled ? 'true' : 'false'}
					aria-invalid={invalid ? 'true' : 'false'}>
					<span className="now-select-trigger-label">{selectedLabel}</span>
					<now-icon
						class={{
							'now-select-trigger-icon': true,
							[`has-${state.decorators}-decorators`]: state.decorators
						}}
						icon="caret-down-outline"
						aria-hidden="true"
					/>
				</button>
				<now-dropdown-list
					slot="content"
					style={getDropdownListConstrain(constrain)}
					items={items}
					search={search}
					manage-selected-items
					selected-items={selectedItem ? [selectedItem] : undefined}
					active-item={focusedItem}
					config-aria={{
						panel: {
							'aria-label': label,
							'aria-required': required ? 'true' : 'false'
						}
					}}
				/>
			</now-popover>
			<div className="now-select-end-slot">
				<slot
					name="end"
					on-click={(e) => {
						if (!opened) {
							e.stopPropagation();
						}
					}}
					on-keydown={(e) => {
						if (!opened) {
							e.stopPropagation();
						}
					}}
					on-slotchange={() => dispatch('PRIVATE#SET_DECORATORS')}
				/>
			</div>
			{renderFormFieldMessages(componentId, messages)}
		</div>
	);
};

/**
 * A form control that enables users to select an option from a dropdown.
 *
 * ```jsx
 * <now-select
 *   label="Select an option"
 *   items={[{id: 'item1', label: 'Item 1'}]}
 *   required />
 * ```
 *
 * @seismicElement now-select
 * @summary Form element that provides a dropdown panel of options for users to select from. 
 * @uib.label Select
 * @uib.icon select
 * @uib.description Form element that provides a dropdown panel of options for users to select from. 
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, items, optional, required, hideRequiredIndicator, disabled, helperContent, search, invalid, autofocus, selectedItem, messages, itemsListConstrain
 * @uib.actions NOW_SELECT#INVALID_SET, NOW_SELECT#SELECTED_ITEM_SET, NOW_SELECT#SEARCH_TERM_SET
 * @uib.innerComponents now-button-iconic, now-dropdown-list, now-icon, now-popover, now-input
 */
createEnhancedElement('now-select', {
	properties: {
		/**
		 * When true, the focus is automatically placed inside the input
		 * on page load.
		 * @type {boolean}
		 * @uib.label Auto-focus
		 * @uib.description Select to automatically place focus inside the input on page load
		 */
		autofocus: {schema: {type: 'boolean'}},
		/**
		 * Controls how to filter the items as the user types into the integrated search input.
		 * - "none": (default) Dropdown list does not display an internal search field.
		 * - "managed": Internal input field is rendered, but no automatic filtering occurs.
		 * Use this if you want to manually filter or search for items as the user types outside of the list.
		 * - "initial": Internal input field is rendered, and items whose label
		 * or sublabel begins with the input value are displayed. This is case-insensitive.
		 * - "contains": Internal input field is rendered, and items whose label
		 * or sublabel contains the input value are displayed. This is case-insensitive.
		 * @type {("none"|"managed"|"initial"|"contains")}
		 * @uib.label Search Behavior
		 * @uib.description Controls how to filter the items as the user types into the integrated search input
		 */
		search: {
			default: 'none',
			schema: {
				type: 'string',
				enum: ['none', 'managed', 'initial', 'contains']
			}
		},
		/**
		 * When true, the user cannot interact with the field
		 * or change its value. Field appears grayed out when disabled.
		 * @type {boolean}
		 * @uib.description Select to disable user click interactions
		 */
		disabled: {schema: {type: 'boolean'}},
		/**
		 * If set, an iconic button with popover appears next to the field
		 * label for displaying additional hint text to the user, such as
		 * password requirements or other instructions for completing the field.
		 * @type {(string|JSX)}
		 * @uib.description If set, an iconic button with popover appears next to the field label for displaying additional hint text to the user, such as password requirements or other instructions for completing the field. Use plain text to allow for translation. If you do not want to add helper content, leave this property empty.
		 * @uib.label Helper text
		 * @uib.fieldType string
		 * @uib.translatable true
		 */
		helperContent: {},
		/**
		 * When true, the required indicator is hidden. Use this for
		 * forms where most fields are required, along with the `optional` flag
		 * for any fields that are not required.
		 * @type {boolean}
		 * @uib.label Hide Required Indicator
		 * @uib.description When true, the required indicator is hidden. Use this for forms where most fields are required, along with the `optional` flag for any fields that are not required.
		 */
		hideRequiredIndicator: {default: false, schema: {type: 'boolean'}},
		/**
		 * Indicates that the field is in an incorrect state,
		 * which is reflected in the field display.
		 * @type {boolean}
		 * @uib.description Select to indicate that the field is in an incorrect state, which is reflected in the field display.
		 */
		invalid: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * An array of `DropdownListItem` or `DropdownListSection` objects that are used as
		 * possible items that the user can select from.
		 * @type {(Array.<DropdownListItem> | Array.<DropdownListSection>)}
		 * @uib.description Array of items in the dropdown
		 * @uib.defaultValue [{"id": "usr1","label": "User 1","sublabel": "user1@servicenow.com","avatarProps": {"userName": "User 1","imageSrc": "http://i.pravatar.cc/24?u=33","presence": "away"}},{"id": "usr2","label": "User 2","sublabel": "user2@servicenow.com","avatarProps": {"userName": "User 2","imageSrc": "http://i.pravatar.cc/24?u=34","presence": "busy"},"disabled": true},{"id": "usr3","label": "User 3","sublabel": "user3@servicenow.com","avatarProps": {"userName": "User 3","imageSrc": "http://i.pravatar.cc/24?u=35","presence": "offline"}}]
		 * @uib.typeMetadata.emptyStateBehavior EMPTY_ARRAY
		 */
		items: {
			required: true,
			schema: itemsSchema
		},
		/**
		 * An object describing additional sizing constrains for the items list popover.
		 *
		 * Valid keys are (all are optional):
		 *
		 *  - width
		 *  - minWidth
		 *  - maxWidth
		 *  - height
		 *  - minHeight
		 *  - maxHeight
		 *
		 * The value for each property should be a positive integer representing
		 * a pixel value that will be applied to the corresponding content style.
		 * Alternatively, you can specify the string `"target"` for width,
		 * minWidth, or maxWidth to have that property match the width of the
		 * input element.
		 *
		 * E.g., To ensure the items list is at least 400x200 pixels:
		 *
		 *  {
		 *		minWidth: 400,
		 *		minHeight: 200
		 *  }
		 *
		 * The default values for this property are:
		 *
		 *  {
		 *		width: "target",
		 *		maxHeight: 300,
		 *  }
		 *
		 * @private
		 * @type {ItemsListConstrain}
		 * @typedef {Object} ItemsListConstrain
		 * @description Sizing constraints to enforce on the items list. For each
		 * property, a number can be specified which sets that constraint in
		 * pixels.  If the value is "target" for width, minWidth, or maxWidth,
		 * the items list is sized to match the width of the input field.
		 * @property {"target"|number} width
		 * @property {"target"|number} minWidth
		 * @property {"target"|number} maxWidth
		 * @property {number} height
		 * @property {number} minHeight
		 * @property {number} maxHeight
		 * @uib.label Item Size Constraints
		 * @uib.description Sizing constraints to enforce on the items list. For each property, a number can be specified which sets that constraint in pixels.  If the value is "target" for width, minWidth, or maxWidth, the items list is sized to match the width of the input field
		 * @uib.defaultValue {}
		 */
		itemsListConstrain: {schema: constrainSchema},
		/**
		 * Label describing the overall purpose of the input field.
		 * @type {string}
		 * @uib.description Label describing the overall purpose of the select field
		 * @uib.defaultValue Label
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Array of inline alert messages that are displayed below the field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: (string|JSX), icon: string}>}
		 * @uib.description Array of inline alert messages that are displayed below the field
		 * @uib.defaultValue []
		 */
		messages: {
			default: [],
			schema: messagesSchema
		},
		/**
		 * Indicates the field is optional and not required to be completed by
		 * the user before completing the form.
		 * @type {boolean}
		 * @uib.description Indicates that the field is optional
		 * @uib.defaultValue false
		 */
		optional: {schema: {type: 'boolean'}},
		/**
		 * Indicates that the user must select an item in the
		 * select dropdown before they can proceed with completing the form.
		 * @type {boolean}
		 * @uib.description Select to indicate that the user must enter a value in the field before proceeding.
		 * @uib.defaultValue false
		 */
		required: {schema: {type: 'boolean'}},
		/**
		 * The ID of the selected item from the `items` array.
		 * @type {(string|number)}
		 * @uib.description The ID of the selected item from the `items` array
		 */
		selectedItem: {manageable: true, schema: {type: ['string', 'number']}}
	},
	dispatches: {
		/**
		 * Dispatched when the field is blurred if the field is `required`
		 * and the user has not selected an option from the dropdown list.
		 * Set the `manage-invalid` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: boolean}}
		 * @uib.description Dispatched when the field is blurred if the field is `required` and the user has not selected an option from the dropdown list
		 */
		'NOW_SELECT#INVALID_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'boolean'}
				}
			}
		},
		/**
		 * Dispatched when the user selects an item from the dropdown list.
		 * Payload key `value` contains the `id` of the item selected.
		 * Payload key `item` contains the `object` of the the item selected.
		 * Set the `manage-selected-item` property to override the default behavior and
		 * handle this action manually.
		 * @type {{value: (string|number), item: Object}}
		 * @uib.description Dispatched when the user selects an item from the dropdown list
		 */
		'NOW_SELECT#SELECTED_ITEM_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: ['string', 'number']},
					item: {type: 'object'}
				}
			}
		},
		/**
		 * Dispatched when the user inputs text in the internal search field.
		 * @type {{value: string}}
		 * @uib.description Dispatched when the user inputs text in the internal search field
		 */
		'NOW_SELECT#SEARCH_TERM_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'string'}
				}
			}
		}
	},
	slots: {
		/**
		 * Slot for field decorator actions on the inline-end (normally right)
		 * side of the select trigger.
		 */
		end: {}
	},
	initialState: {
		opened: false,
		focusedItem: undefined,
		decorators: 0
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect({action, updateState, dispatch, state, properties}) {
				if (properties.disabled) return;

				const {key} = action.payload.event;
				const selectItemKeys = [' ', 'Enter'];

				const flattened = flattenItems(properties.items);

				const focusedItemIndex = flattened.findIndex((item) => {
					return item.id === state.focusedItem;
				});
				const lastItem = flattened.length - 1;

				if (selectItemKeys.includes(key)) {
					if (!state.opened) {
						action.preventDefault();
						updateState({opened: true});
					} else {
						if (properties.search !== 'none') return;
						action.preventDefault();
						const focusedItem = flattened[focusedItemIndex];
						if (focusedItem && !focusedItem.disabled) {
							updateState({opened: false, focusedItem: undefined});
							dispatch(() => {
								return {
									type: 'NOW_SELECT#SELECTED_ITEM_SET',
									payload: {
										value: focusedItem.id,
										item: focusedItem
									}
								};
							});
						}
					}
				} else {
					let nextFocus;
					if (key === 'ArrowDown') {
						action.preventDefault();
						if (!state.opened) {
							updateState({opened: true});
						}
						// dropdown list handles focus for us if it has a search field
						if (properties.search !== 'none') return;
						nextFocus = Math.max(Math.min(focusedItemIndex + 1, lastItem), 0);
					} else if (key === 'ArrowUp') {
						action.preventDefault();
						if (!state.opened) {
							updateState({opened: true});
						}
						// dropdown list handles focus for us if it has a search field
						if (properties.search !== 'none') return;
						nextFocus = Math.max(focusedItemIndex - 1, 0);
					} else if (key === 'Home') {
						if (!state.opened) return;
						action.preventDefault();
						nextFocus = 0;
					} else if (key === 'End') {
						if (!state.opened) return;
						action.preventDefault();
						nextFocus = lastItem;
					} else {
						if (!state.opened || properties.search !== 'none') return;
						// For printable chars, get indices of all items that start with key
						const filtered = flattened.reduce((result, item, i) => {
							if (item.label.toLowerCase().startsWith(key.toLowerCase())) {
								result.push(i);
							}
							return result;
						}, []);
						// and loop focus over them on subsequent key presses
						// from the currently focused (or first) matching item
						if (filtered.length > 0) {
							const prev = filtered.findIndex((i) => i === focusedItemIndex);
							const next = (prev + 1) % filtered.length;
							nextFocus = filtered[next];
						}
					}
					if (flattened[nextFocus]) {
						updateState({
							focusedItemIndex: nextFocus,
							focusedItem: flattened[nextFocus].id
						});
					}
				}
			}
		}
	],
	actionHandlers: {
		[COMPONENT_DOM_TREE_READY]: ({host, properties}) => {
			if (properties.autofocus) {
				focusTrigger(host);
			}
		},
		'NOW_POPOVER#OPENED_SET': ({action, properties, dispatch, updateState}) => {
			const {value: opened, isListbox} = action.payload;
			const {disabled} = properties;
			if (!isListbox || disabled) return;
			updateState({opened, focusedItem: null});
			if (!opened) {
				dispatch(() => {
					return {
						type: 'PRIVATE#RUN_VALIDATION'
					};
				});
			}
		},
		'NOW_POPOVER#CONTENT_VISIBLE': ({host}) => {
			const dropdownList = host.shadowRoot.querySelector('now-dropdown-list');
			dropdownList.focus();
		},
		'NOW_DROPDOWN_LIST#ACTIVE_ITEM_SET': ({action, updateState}) => {
			updateState({focusedItem: action.payload.value});
		},
		'NOW_DROPDOWN_LIST#SELECTED_ITEMS_SET': ({
			host,
			action,
			dispatch,
			updateState
		}) => {
			updateState({opened: false, focusedItem: null});
			focusTrigger(host);
			const selectedItemId = action.payload.value[0];
			dispatch(() => {
				return {
					type: 'NOW_SELECT#SELECTED_ITEM_SET',
					payload: {value: selectedItemId, item: action.payload.items[0]}
				};
			});
			dispatch(() => {
				return {
					type: 'PRIVATE#RUN_VALIDATION'
				};
			});
		},
		'NOW_DROPDOWN_LIST#SEARCH_TERM_SET': makeRedispatchEffect(
			'NOW_SELECT#SEARCH_TERM_SET'
		),
		'PRIVATE#RUN_VALIDATION': {
			stopPropagation: true,
			effect: ({dispatch, properties}) => {
				const {required, selectedItem, invalid} = properties;
				const isInvalid = !selectedItem;
				dispatch(() => {
					return {
						type: 'NOW_SELECT#INVALID_SET',
						payload: {value: isInvalid},
						shouldDispatch:
							(required && isInvalid && !invalid) || (!isInvalid && invalid)
					};
				});
			}
		},
		'PRIVATE#SET_DECORATORS': {
			stopPropagation: true,
			effect: ({host, updateState}) => {
				const slotEl = host.shadowRoot.querySelector("[name='end']");
				const slottedNodes = slotEl.assignedNodes();

				const decorators = slottedNodes.reduce((numDecorators, node) => {
					if (node.tagName.includes('ICON')) {
						return numDecorators + 1;
					}

					const icons = node.querySelectorAll('now-icon, now-button-iconic');
					const numIcons = icons.length;

					return numIcons ? numDecorators + numIcons : numDecorators;
				}, 0);

				updateState({decorators: decorators > 2 ? 2 : decorators});
			}
		}
	},
	behaviors: [{behavior: focusBehavior}],
	styles,
	view
});
